/* Clean Technical Trading Scanner - OpenAI Style */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap');

:root {
  /* Dark Technical Color System */
  --bg-primary: #000000;
  --bg-secondary: #0a0a0a;
  --bg-tertiary: #141414;
  --bg-card: #000000;
  --bg-hover: #1a1a1a;
  --bg-input: #0a0a0a;

  /* Bright Technical Accent Colors */
  --accent-primary: #00ff88;
  --accent-secondary: #00cc6a;
  --accent-success: #00ff41;
  --accent-warning: #ffaa00;
  --accent-danger: #ff4444;
  --accent-info: #00aaff;

  /* Text Colors */
  --text-primary: #ffffff;
  --text-secondary: #a0a0a0;
  --text-muted: #666666;
  --text-disabled: #333333;

  /* Border Colors */
  --border-primary: #222222;
  --border-secondary: #141414;
  --border-focus: #00ff88;

  /* Technical UI */
  --radius: 6px;
  --font-mono: 'SF Mono', 'Monaco', 'Inconsolata', 'Roboto Mono', monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: var(--bg-primary);
  color: var(--text-primary);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: var(--bg-primary);
}

/* Clean Technical Header */
.app-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 20px;
  background: var(--bg-secondary);
  border-bottom: 1px solid var(--border-primary);
  position: sticky;
  top: 0;
  z-index: 100;
}

.layout-toggle {
  display: flex;
  gap: 0;
  background: var(--bg-tertiary);
  border-radius: var(--radius);
  padding: 3px;
  border: 1px solid var(--border-primary);
  position: relative;
  overflow: hidden;
}

.toggle-btn {
  padding: 10px 20px;
  background: transparent;
  color: var(--text-secondary);
  border: none;
  cursor: pointer;
  font-family: 'Inter', sans-serif;
  font-size: 12px;
  font-weight: 500;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: calc(var(--radius) - 2px);
  white-space: nowrap;
  position: relative;
  z-index: 2;
  flex: 1;
  text-align: center;
}

.toggle-btn:hover:not(.active) {
  color: var(--text-primary);
  background: rgba(255, 255, 255, 0.05);
  transform: translateY(-1px);
}

.toggle-btn.active {
  background: var(--accent-primary);
  color: #000000;
  font-weight: 600;
  box-shadow: 0 0 20px rgba(0, 255, 136, 0.3);
  transform: translateY(0);
}

.toggle-btn.active::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.1), transparent);
  animation: shimmer 2s ease-in-out infinite;
  border-radius: inherit;
}

@keyframes shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

/* Technical Voice Toggle */
.voice-toggle {
  display: flex;
  align-items: center;
}

.voice-toggle-label {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  font-size: 13px;
  color: var(--text-secondary);
  font-weight: 500;
  transition: color 0.15s ease;
}

.voice-toggle-label:hover {
  color: var(--text-primary);
}

.voice-toggle-label input[type="checkbox"] {
  width: 16px;
  height: 16px;
  accent-color: var(--accent-primary);
  background: var(--bg-secondary);
  border: 1px solid var(--border-primary);
}

.voice-toggle-text {
  font-size: 13px;
  font-weight: 500;
}

.voice-toggle-label:has(input:checked) .voice-toggle-text {
  color: var(--accent-primary);
}

.voice-status {
  font-size: 10px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: var(--radius);
  margin-left: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: all 0.15s ease;
}

.voice-status.on {
  color: white;
  background: var(--accent-primary);
}

.voice-status.off {
  color: var(--text-muted);
  background: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
}

/* Optimized Grid Layout */
.scanner-grid {
  flex: 1;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: 1px;
  background: var(--border-primary);
  height: calc(100vh - 60px);
  overflow: hidden;
}

/* Unified Layout */
.unified-layout {
  flex: 1;
  height: calc(100vh - 60px);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

/* Historical Layout */
.historical-layout {
  flex: 1;
  height: calc(100vh - 60px);
  overflow: hidden;
  padding: 20px;
  background: var(--bg-primary);
}

/* Technical Focus States */
.scanner-window:focus-within {
  outline: 2px solid var(--accent-primary);
  outline-offset: -2px;
}

/* Responsive Grid System */
@media (max-width: 1600px) {
  .scanner-grid {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}

@media (max-width: 1200px) {
  .scanner-grid {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(4, 1fr);
  }

  .app-controls {
    padding: 10px 16px;
  }

  .toggle-btn {
    padding: 6px 12px;
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .scanner-grid {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(8, minmax(200px, 1fr));
    height: auto;
    min-height: calc(100vh - 60px);
  }

  .app-controls {
    flex-direction: column;
    gap: 12px;
    padding: 12px 16px;
  }

  .layout-toggle {
    width: 100%;
  }

  .toggle-btn {
    flex: 1;
    text-align: center;
  }
}

/* Technical Loading States */
@keyframes technicalPulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.6; }
}

.loading {
  animation: technicalPulse 1.5s ease-in-out infinite;
}

/* Loading overlay for initialization */
.loading-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
  padding: 16px 24px;
  z-index: 10;
  display: flex;
  align-items: center;
  gap: 12px;
}

.loading-text {
  color: var(--accent-primary);
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-family: var(--font-mono);
  animation: technicalPulse 2s ease-in-out infinite;
}

.loading-dot {
  color: var(--accent-primary);
  font-size: 8px;
  animation: technicalPulse 1s ease-in-out infinite;
  margin-left: 4px;
}

/* Accessibility and UX enhancements */
.toggle-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.toggle-btn:focus-visible {
  outline: 2px solid var(--accent-primary);
  outline-offset: 2px;
}

/* High contrast improvements */
@media (prefers-contrast: high) {
  .loading-overlay {
    border-width: 2px;
    background: var(--bg-primary);
  }
}

/* Clean Dark Scrollbars */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-primary);
}

::-webkit-scrollbar-thumb {
  background: var(--bg-tertiary);
  border-radius: var(--radius);
  border: 1px solid var(--border-primary);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--accent-primary);
  box-shadow: 0 0 8px rgba(0, 255, 136, 0.3);
}

/* Accessibility */
@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  :root {
    --border-primary: #444444;
    --text-secondary: #ffffff;
    --accent-primary: #00ff00;
    --bg-secondary: #000000;
    --bg-tertiary: #111111;
  }
}

/* Print styles */
@media print {
  .app-controls {
    display: none;
  }

  .scanner-grid {
    display: block;
    height: auto;
  }

  .scanner-window {
    break-inside: avoid;
    margin-bottom: 20px;
    border: 1px solid #333;
  }
}