/* Clean Technical Scanner Window - OpenAI Style */
.scanner-window {
  background: var(--bg-secondary);
  border-right: 1px solid var(--border-primary);
  border-bottom: 1px solid var(--border-primary);
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif;
  font-size: 13px;
  line-height: 1.4;
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  transition: background-color 0.15s ease;
  position: relative;
}

.scanner-window:hover {
  background: var(--bg-hover);
}

/* Clean Technical Header */
.window-header {
  background: var(--bg-secondary);
  border-bottom: 1px solid var(--border-primary);
  padding: 12px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  user-select: none;
  min-height: 48px;
}

.window-controls {
  display: none; /* Remove the dots completely */
}

.window-title {
  color: var(--text-primary);
  font-weight: 600;
  font-size: 14px;
  text-align: left;
  flex: 1;
  letter-spacing: -0.01em;
}

.alert-count {
  min-width: 40px;
  text-align: right;
}

.alert-badge {
  background: var(--accent-primary);
  color: white;
  font-size: 11px;
  font-weight: 600;
  padding: 4px 8px;
  border-radius: var(--radius);
  text-transform: uppercase;
  letter-spacing: 0.5px;
  min-width: 32px;
  text-align: center;
  font-family: var(--font-mono);
}

/* Technical Pattern Description */
.pattern-description {
  background: var(--bg-tertiary);
  padding: 8px 16px;
  border-bottom: 1px solid var(--border-primary);
  color: var(--text-secondary);
  font-size: 11px;
  font-weight: 400;
  line-height: 1.3;
  font-family: var(--font-mono);
}

/* Clean Content Area */
.window-content {
  flex: 1;
  padding: 12px 16px;
  overflow-y: auto;
  background: var(--bg-secondary);
  color: var(--text-primary);
  scroll-behavior: smooth;
}

/* Technical Scrollbar */
.window-content::-webkit-scrollbar {
  width: 4px;
}

.window-content::-webkit-scrollbar-track {
  background: transparent;
}

.window-content::-webkit-scrollbar-thumb {
  background: var(--border-primary);
  border-radius: 2px;
}

.window-content::-webkit-scrollbar-thumb:hover {
  background: var(--text-muted);
}

/* Clean No Alerts State */
.no-alerts {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: var(--text-muted);
  text-align: center;
  padding: 20px;
  font-size: 12px;
}

.terminal-line {
  margin-bottom: 8px;
  font-family: var(--font-mono);
  font-size: 11px;
  opacity: 0.8;
}

.prompt {
  color: var(--accent-primary);
  font-weight: 600;
}

.command {
  color: var(--text-secondary);
  margin-left: 4px;
}

.terminal-cursor {
  color: var(--accent-primary);
  animation: blink 1.2s infinite;
  font-size: 11px;
  font-weight: bold;
}

@keyframes blink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0.3; }
}

/* Technical Alert Lines */
.alert-line {
  margin-bottom: 12px;
  padding: 12px;
  background: var(--bg-tertiary);
  border: 1px solid var(--border-primary);
  border-radius: var(--radius);
  border-left: 3px solid var(--window-color, var(--accent-primary));
  transition: all 0.15s ease;
  position: relative;
}

.alert-line:hover {
  background: var(--bg-hover);
  border-color: var(--border-secondary);
}

.alert-line.live {
  border-left-color: var(--accent-success);
}

.alert-line.live::after {
  content: 'LIVE';
  position: absolute;
  top: 8px;
  right: 8px;
  background: var(--accent-success);
  color: white;
  font-size: 8px;
  font-weight: 700;
  padding: 2px 4px;
  border-radius: 2px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-family: var(--font-mono);
}

.alert-line.historical {
  border-left-color: var(--text-muted);
  opacity: 0.8;
}

.alert-line.historical::after {
  content: 'HIST';
  position: absolute;
  top: 8px;
  right: 8px;
  background: var(--text-muted);
  color: white;
  font-size: 8px;
  font-weight: 600;
  padding: 2px 4px;
  border-radius: 2px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-family: var(--font-mono);
}

/* Technical Alert Header */
.alert-header {
  display: flex;
  gap: 12px;
  align-items: center;
  margin-bottom: 6px;
  font-size: 11px;
  flex-wrap: wrap;
}

.timestamp {
  color: var(--text-muted);
  font-family: var(--font-mono);
  font-size: 10px;
  background: var(--bg-primary);
  padding: 2px 4px;
  border-radius: 2px;
  border: 1px solid var(--border-primary);
}

.symbol {
  color: var(--text-primary);
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-family: var(--font-mono);
}

.price {
  color: var(--accent-success);
  font-weight: 600;
  font-size: 11px;
  font-family: var(--font-mono);
}

.volume {
  color: var(--text-secondary);
  font-size: 10px;
  font-weight: 500;
  font-family: var(--font-mono);
}

.historical-tag {
  color: var(--text-muted);
  font-size: 9px;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 0.5px;
  font-family: var(--font-mono);
}

/* Technical Alert Detail */
.alert-detail {
  display: flex;
  align-items: flex-start;
  gap: 8px;
  margin-top: 6px;
}

.alert-detail .prompt {
  color: var(--accent-primary);
  font-weight: 600;
  flex-shrink: 0;
  font-size: 10px;
  font-family: var(--font-mono);
}

.detail-text {
  color: var(--text-secondary);
  line-height: 1.4;
  font-size: 11px;
  font-weight: 400;
}

/* Technical Status Bar */
.window-status {
  background: var(--bg-tertiary);
  border-top: 1px solid var(--border-primary);
  padding: 8px 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 10px;
  color: var(--text-secondary);
  min-height: 32px;
}

.status-text {
  font-family: var(--font-mono);
  font-weight: 500;
}

.pattern-type {
  color: var(--accent-primary);
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  font-family: var(--font-mono);
}

/* Technical Indicators */
.alert-line::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 2px;
  height: 100%;
  background: var(--window-color, var(--accent-primary));
  opacity: 0.6;
}

.alert-line.live::before {
  background: var(--accent-success);
  opacity: 1;
  animation: technicalPulse 2s infinite;
}

@keyframes technicalPulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.4; }
}

/* Priority Indicators */
.scanner-window[style*="--window-color: #ffa500"] .window-title::before,
.scanner-window.high-priority .window-title::before {
  content: '●';
  color: var(--accent-warning);
  margin-right: 6px;
  font-size: 8px;
}

.scanner-window[style*="--window-color: #00d4ff"] .window-title::before,
.scanner-window.medium-priority .window-title::before {
  content: '●';
  color: var(--accent-info);
  margin-right: 6px;
  font-size: 8px;
}

.scanner-window[style*="--window-color: #ff6b9d"] .window-title::before,
.scanner-window.low-priority .window-title::before {
  content: '●';
  color: #ff6b9d;
  margin-right: 6px;
  font-size: 8px;
}

/* Responsive Design */
@media (max-width: 1200px) {
  .scanner-window {
    font-size: 12px;
  }

  .window-header {
    padding: 10px 14px;
    min-height: 44px;
  }

  .window-title {
    font-size: 13px;
  }

  .window-content {
    padding: 10px 14px;
  }

  .alert-line {
    padding: 10px;
    margin-bottom: 10px;
  }
}

@media (max-width: 768px) {
  .scanner-window {
    font-size: 11px;
  }

  .window-header {
    padding: 8px 12px;
    min-height: 40px;
  }

  .window-title {
    font-size: 12px;
  }

  .window-content {
    padding: 8px 12px;
  }

  .alert-line {
    padding: 8px;
    margin-bottom: 8px;
  }

  .alert-header {
    gap: 8px;
  }
}

@media (max-width: 480px) {
  .scanner-window {
    font-size: 10px;
  }

  .window-header {
    padding: 6px 10px;
    min-height: 36px;
  }

  .window-title {
    font-size: 11px;
  }

  .alert-badge {
    font-size: 9px;
    padding: 2px 6px;
  }
}